.wrapper {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
  border-radius: 20px;
  background-color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: space-around;
}


.btn button {
  width: 150px;
  height: 40px;
  font-size: 20px;
  background-color: aquamarine;
  border-radius: 20px;
  margin: 0 5px;
}

.header {
  display: flex;
  align-items: center;
}

.nav a{
  margin-left: 20px;
  font-size: 30px;
  text-decoration-line: none;
  color: black;
}

.result {
  width: 800px;
  min-height: 100px;
  margin: 0 auto;
  background-color: aliceblue;
  border-radius: 20px;
  margin-top: 20px;
}
.result h2 {
  margin-left: 40px;
  padding-top: 20px;
}

.user{
  display: flex;
  align-items: center;
  font-size: 25px;
}

.user p {
  margin-left: 40px;
  margin-right: 10px;
}

.use {
  display: inline;
}



